import React from 'react'
import * as d3 from 'd3'
import { message, Card, List, Button, Tooltip} from 'antd'
import { UndoOutlined, CloseOutlined} from '@ant-design/icons'
import './index.scss'
class TreeInfoPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }
    componentDidMount() {
        this.props.onRef(this);
    }
    revokeCutNode = () => {
        const { cutNode, onTreeSettingChange } = this.props,
            { cutNode_node, cutNode_dom } = cutNode
        if (cutNode_node) {
            d3.select(cutNode_dom).classed('cutting', false);
            onTreeSettingChange({ data: null, dom: null }, "cutNode")
            message.success('取消剪切!');
        }
    }
    revokeCopyNode = () => {
        const { copyNode, onTreeSettingChange } = this.props
        if (copyNode) {
            onTreeSettingChange(null, "copyNode")
            message.success('取消复制!');
        }
    }
    render() {
        const { cur_treeData, infoBarVisible, treeNodeCount, treeDepth,cutNode,selectNode,copyNode, onTreeSettingChange } = this.props
        const stateData = [
            { title: '节点总数: ', content: treeNodeCount ? treeNodeCount : '', icon: null },
            { title: '总层次: ', content: treeDepth ? treeDepth + 1 : '', icon: null },
            { title: '当前节点数: ', content: cur_treeData.length, icon: null },
            { title: '当前层次: ', content: cur_treeData.length > 0 ? cur_treeData[cur_treeData.length - 1].depth + 1 : '', icon: null },
            {
                title: '当前选中节点: ', content: selectNode ? selectNode._node.data.name : "无", icon: null
            },
            {
                title: '当前复制节点: ', content: copyNode ? copyNode.data.name : "无", icon: <Tooltip placement="bottom" title="取消复制">
                    <Button shape="circle" size="small" className="treeStateInfo-list-item-button" onClick={this.revokeCopyNode} icon={<UndoOutlined />} />
                </Tooltip>
            },
            {
                title: '当前剪切节点: ', content: cutNode.cutNode_node ? cutNode.cutNode_node.data.name : "无", icon: <Tooltip placement="bottom" title="取消剪切">
                    <Button shape="circle" size="small" className="treeStateInfo-list-item-button" onClick={this.revokeCutNode} icon={<UndoOutlined />} />
                </Tooltip>
            }
        ]
        return (
            <Card
                className="tree-info-card"
                style={infoBarVisible ? { display: 'block' } : { display: 'none' }}
                title="统计信息"
                size="small"
                headStyle={{ fontSize: "16px", fontWeight: 'bold' }} bodyStyle={{ padding: "6px 0" }}
                extra={<Button shape="circle" size="small" icon={<CloseOutlined />} onClick={() => onTreeSettingChange(null, "infoBarVisible")}></Button>}>
                <List
                    size="small"
                    dataSource={stateData}
                    split={false}
                    renderItem={item => {
                        return (
                            <>
                                <List.Item className="tree-info-list-item" >
                                    <List.Item.Meta
                                        avatar={item.title + item.content}
                                    />
                                    {item.icon}
                                </List.Item>
                            </>
                        )
                    }

                    }
                />

            </Card>
        )
    }
}

export default TreeInfoPanel;