import React from 'react';
import { message } from 'antd'
import './tree.scss';
import * as d3 from 'd3'
import TreeInfoPanel from './TreeInfoPanel/index'
import TreeSettingsPanel from './TreeSettingsPanel/index'
import ContextMenu from './ContextMenu/index'
import ExternalButton from './ExternalButton/index'
class Tree extends React.Component {
    static displayName = 'Tree';
    constructor(props) {
        super(props);
        this._data = d3.hierarchy(JSON.parse(JSON.stringify(this.props.treeData)));
        this._tree = d3.tree();
        this._data.x0 = 0;
        this._data.y0 = 0;
        this._svg = null;
        this._svgTree = null;
        this._zoom = d3.zoom();
        this._duration = 450;
        this._diagonal = null; // 边位置
        this.mainview = React.createRef(); // 绑定div容器Dom
        this._svgWidth = null;  // svg宽度
        this._svgHeight = null; // svg高度
        this.updateLink = null;
        this.updateNode = null;
        this.initZoom = true;
        this.dragObj = {
            // 拖拽时的节点
            current: null,
            // 拖拽时，所依附的目标节点
            target: null,
            // 最后一次可拖拽到目标的目标节点
            lastTarget: null,
            /**
             * 连线的起点和终点
             */
            line: [[0, 0], [0, 0]],
        };
        this.transformParam = {
            k: 1,
            x: 0,
            y: 0,
        };
        this.contextMenuRef = null; // contextMenu的react
        this.treeRef = null;
        this.virtualContainer = d3.create('virtualContainer');
        const treeNodesList = this._data.descendants()
        this.state = {
            cur_treeData: [],
            selectNode: null, // 选中节点 {_node: , _dom:}
            layoutSet: {
                nodeSpace: 50,
                linkLength: 400,
                nodeSize: 10,
                linkWidth: 1,
                labelSize: 15,
            },
            colorSet: {
                nodeColor: "#ee8f44",
                linkColor: "#ccc",
                labelColor: "#000",
            },
            nodeFocus: true,
            autoZoomTree: false,
            infoBarVisible: true,
            isNodeLazyLoading: false,
            copyNode: null,
            cutNode: {
                cutNode_node: null,
                cutNode_dom: null
            },
            treeNodeCount: treeNodesList.length,
            treeDepth: treeNodesList.slice(-1)[0].depth,
            historyEdit: [],
        };
    }
    componentDidMount() {
        // 渲染后
        this.treeRef = this
        this.initTree()
    }

    componentDidUpdate(prevProps, prevState) {
        // dom渲染后 state更新后,执行
        const { layoutSet, colorSet, autoZoomTree,isNodeLazyLoading } = this.state
        if (layoutSet !== prevState.layoutSet) {
            if (layoutSet.linkLength !== prevState.layoutSet.linkLength ||
                layoutSet.nodeSpace !== prevState.layoutSet.nodeSpace ||
                layoutSet.nodeSize !== prevState.layoutSet.nodeSize) {
                this._tree.nodeSize([2 * layoutSet.nodeSize + 1 + layoutSet.nodeSpace, 70])
                this.update()
                if (layoutSet.nodeSize !== prevState.layoutSet.nodeSize) {
                    this.updateTreeDisplay()
                }
            } else {
                this.updateTreeDisplay()
            }
        } else if (colorSet !== prevState.colorSet) {
            this.updateTreeDisplay()
        }
        // 开启自动缩放
        if (autoZoomTree !== prevState.autoZoomTree && autoZoomTree) {
            this.onAutoZoomTree();
        }
        if (isNodeLazyLoading !== prevState.isNodeLazyLoading && isNodeLazyLoading) {
            this.update();
        }
    }

    initTree = () => {
        const { layoutSet } = this.state
        // 获取当前窗口的宽高
        this._svgWidth = this.mainview.current.offsetWidth
        this._svgHeight = this.mainview.current.offsetHeight

        // 初始化树布局
        this._tree.nodeSize([2 * layoutSet.nodeSize + 1 + layoutSet.nodeSpace, 70])
        this._diagonal = d3.linkHorizontal()  // 横向贝塞尔曲线：d3.linkHorizontal() 纵向贝塞尔曲线：d3.linkVertical()
            .x(function (d) { return d.y; })  // 水平：d.y 垂直：d.x
            .y(function (d) { return d.x; }); // 水平：d.x 垂直：d.y

        // 缩放初始化
        this._zoom.scaleExtent([0.1, 5]).on('zoom', this.zoomed).on('end', this.zoomended);

        this.collapseAllElements(this._data)
        this._svg = d3.select('.mainview')
            .append("svg")
            .attr("width", "100%")
            .attr("height", "100%")
            .classed("svg-tree", true)
            .on('contextmenu', (e) => {
                e.preventDefault();
            })
            .call(this._zoom)
            .on('dblclick.zoom', null)
            .on("click", this.handleSvgClick);

        this._svgTree = this._svg
            .append("g")
            .attr("class", "svg-tree-content");

        this._zoom.translateTo(this._svg, -100, -(this._svgHeight / 2))
        // 初始化结束
        this.initZoom = false
        this.update(this._data)

    }
    update(source, action) {
        const { layoutSet, colorSet, autoZoomTree, treeDepth, isNodeLazyLoading } = this.state
        const treeData = this._tree(this._data)
        let _this = this
        let nodes = treeData.descendants();
        let links = treeData.links();

        this.setState({ cur_treeData: nodes })

        // 为固定深度规格化。
        nodes.forEach(d => {
            d.y = (d.depth * layoutSet.linkLength);
        });
        if (isNodeLazyLoading) {
            // 过滤超出当前可视窗口的节点
            nodes = nodes.filter(d => {
                const d_y = d.x * _this.transformParam.k + _this.transformParam.y,
                    d_x = d.y * _this.transformParam.k + _this.transformParam.x;
                if (d_y < 0 || d_y > _this._svgHeight || d_x < 0 || d_x > _this._svgWidth) {
                    return false
                } else {
                    return true
                }
            })
            links = links.filter(d => {
                const d_source_y = d.source.x * _this.transformParam.k + _this.transformParam.y,
                    d_target_y = d.target.x * _this.transformParam.k + _this.transformParam.y
                if ((d_source_y < 0 && d_target_y < 0) || (d_source_y > _this._svgHeight && d_target_y > _this._svgHeight)) {
                    return false
                } else {
                    return true
                }
            })
        }

        // 设置节点ID
        let node = this._svgTree.selectAll("g.node")
            .data(nodes, d => d.data.id);

        // 设置边ID
        let link = this._svgTree.selectAll("path.link")
            .data(links, d => d.target.data.id);

        //链接退出隐藏，转换到父节点的位置。
        link.exit().transition()
            .duration(this._duration)
            .attr("d", (d) => {
                const o = {
                    x: d.source.x,
                    y: d.source.y
                };
                return this._diagonal({
                    source: o,
                    target: o
                });
            })
            .remove();

        // 在父对象的上一个位置添加任何新链接。
        let linkEnter = link.enter()
            .insert("path", "g")
            .attr("id", d => d.target.data.id)
            .attr("class", "link")
            .attr("d", (d) => {
                let o = {}
                if (!d.source.x0) {
                    o = {
                        x: d.source.x,
                        y: d.source.y
                    };
                } else {
                    o = {
                        x: d.source.x0,
                        y: d.source.y0
                    };
                }
                return this._diagonal({
                    source: o,
                    target: o
                });
            })
            .style("fill", 'none')
            .style("stroke", d => d.target.class === "found" ? "#ff4136" : colorSet.linkColor)
            .style("stroke-width", d => d.target.class === "found" ? layoutSet.linkWidth * 2 : layoutSet.linkWidth)


        // 过渡链接到目标节点位置。
        this.updateLink = linkEnter.merge(link)

        this.updateLink.transition()
            .duration(this._duration)
            .attr("d", d3.linkHorizontal()
                .x(d => d.y)
                .y(d => d.x));


        let upLoadClicktag = 0; // 控制点击的变量 防止多次点击
        //在父节点的上一个位置添加新节点。
        let nodeEnter = node.enter()
            .append('g')
            .attr("id", d => d.data.id)
            .attr("class", function (d) {
                if (d.selected) {
                    // 重新绑定选择节点
                    _this.setState({ selectNode: { _node: d, _dom: this } })
                }
                return "node"
            })
            .attr("transform", (d) => {
                if (d.parent !== null) {
                    if (!d.parent.x0) {
                        return "translate(" + (d.parent.y) + "," + (d.parent.x) + ")";
                    } else {
                        return "translate(" + (d.parent.y0) + "," + (d.parent.x0) + ")";
                    }
                } else {
                    if (!d.y0) {
                        return "translate(" + (d.y) + "," + (d.x) + ")";
                    } else {
                        return "translate(" + (d.y0) + "," + (d.x0) + ")";
                    }

                }

            })
            .on("click", function (e, d) {
                //第一次点击为true
                if (upLoadClicktag === 0) {
                    upLoadClicktag = 1;
                    _this.handleSelectNode(d, this)
                    _this._click(d)
                    //300ms过后可以再次点击
                    setTimeout(function () { upLoadClicktag = 0 }, 300);
                }

            })
            .call(this.draggable())
            // 添加mouseover事件
            .on("mouseout", () => {
                // 添加浮层hidden样式，隐藏浮层
                d3.select(".node-tooltip").classed("hidden", true);
            })
            .on("contextmenu", function (event, d) { // 显示右键节点菜单
                _this.handleSelectNode(d, this)
                _this.onContextmenu(event, d)
            })

        // 更新tree信息，并获取当前层次
        // 设置节点颜色渐变
        let linear = d3.scaleLinear().domain([0, treeDepth]).range([0, 1]),
            compute = d3.interpolateRgb.gamma(2.2)("#0a1fba", "#D3BDEC");

        // "#2c3e50", "#bdc3c7" #640D8C
        // 创建节点
        nodeEnter
            .append("circle")
            .attr("class", "node-circle")
            .attr("r", d => d.selected ? layoutSet.nodeSize * 1.5 : layoutSet.nodeSize)
            .style("stroke", d => d.selected ? "#EE1C25" : colorSet.nodeColor)
            .style("stroke-width", d => d.selected ? 4 : d._children ? 2 : 0)
            .style("fill", d => d.class === "found" ? "#ff4136" : compute(linear(d.depth)));

        // 创建拖拽缓冲区节点
        nodeEnter.append('circle')
            .attr('class', 'ghost-circle')
            .attr('r', 0)
            .on('mouseenter', function (event, d) {
                if (_this.dragObj.current && d !== _this.dragObj.current && !_this.isParent(d) && _this.dragObj.current.parent !== d) {
                    // 拖拽至目标节点上——节点加本文的g容器区域
                    _this.dragObj.lastTarget = d;
                    _this.dragObj.target = d;
                    d3.select(this).classed('dragging-target', true);
                }
            })
            .on('mouseleave', function (event, d) {
                d3.select(this).classed('dragging-target', false);
                _this.dragObj.target = null;
                _this.removeLink();
            });

        // 创建文本
        nodeEnter.append("text")
            .attr("x", layoutSet.nodeSize + 4)
            .attr("dy", "0.31em")
            .attr("text-anchor", "start")
            .style("font-size", layoutSet.labelSize + "px")
            .text((d) => d.data.name)

        // 更新
        this.updateNode = nodeEnter.merge(node)

        // 将节点转换到新位置
        this.updateNode.transition()
            .duration(this._duration)
            .attr("transform", (d) => {
                return "translate(" + d.y + "," + d.x + ")";
            });


        // 隐藏拖拽缓冲区
        // updateNode.select('circle.ghost-circle')
        //     .attr('r', 0)


        // 将退出节点移动到父节点的位置。
        let nodeExit = node.exit().transition()
            .duration(this._duration)
            .attr("transform", (d) => {
                if (d.parent !== null) {
                    return "translate(" + d.parent.y + "," + d.parent.x + ")";
                } else {
                    return "translate(" + d.y + "," + d.x + ")";
                }

            })
            .remove();

        // 退出时，将节点圆的大小减小为0
        nodeExit.select('circle')
            .attr('r', 1e-6);

        // 退出时减少文本标签的不透明度
        nodeExit.select('text')
            .style('fill-opacity', 1e-6);

        //将旧位置隐藏起来，以便过渡。
        nodes.forEach((d) => {
            d.x0 = d.x;
            d.y0 = d.y;
        });

        if (['remove', 'add', "cut", 'drag'].indexOf(action) !== -1) {
            // 层次渐进
            this.updateNode.select('.node-circle')
                .style("fill", d => compute(linear(d.depth)))
                .style("stroke-width", d => d.selected ? 4 : d._children ? 2 : 0);
        } else if (["expandAllElements", "collapseAllElements"].indexOf(action) !== -1) {
            this.updateNode.select('.node-circle')
                .style("stroke-width", d => d._children ? 2 : 0);
        }
        // 自动缩放
        if (autoZoomTree) {
            setTimeout(() => {
                this.onAutoZoomTree();
            }, 500);
        }
    }
    // 更新样式
    updateTreeDisplay() {
        const { colorSet, layoutSet, treeDepth } = this.state

        this.updateLink
            .style("stroke", d => d.target.class === "found" ? "#ff4136" : colorSet.linkColor)
            .style("stroke-width", d => d.target.class === "found" ? layoutSet.linkWidth * 2 : layoutSet.linkWidth)

        let linear = d3.scaleLinear().domain([0, treeDepth]).range([0, 1]),
            compute = d3.interpolateRgb.gamma(2.2)("#0a1fba", "#D3BDEC");
        // 更新节点
        this.updateNode.select('.node-circle')
            .attr("r", layoutSet.nodeSize)
            .style("stroke", colorSet.nodeColor)
            .style("stroke-width", d => d._children ? 2 : 0)
            .style("fill", d => d.class === "found" ? "#ff4136" : compute(linear(d.depth)));

        // 更新文本
        this.updateNode.select('text')
            .attr("x", layoutSet.nodeSize + 4)
            .style("font-size", layoutSet.labelSize + "px")
            .text((d) => d.data.name)

    }
    zoomed = (event) => {
        this._svgTree.attr('transform', this.transformParam = event.transform);
    }
    zoomended = (event) => {
        // 拖拽结束动态显示节点
        if (!this.initZoom) {
            this.update()
        }
    }
    // 缩放居中
    onAutoZoomTree() {
        const viewBox = this._svg.node().getBBox(),
            pre_scale = this.transformParam.k;

        if (viewBox.width > this._svgWidth || viewBox.height > this._svgHeight) {
            const next_scale = Math.min((this._svgWidth - 100) / viewBox.width * pre_scale, (this._svgHeight - 100) / viewBox.height * pre_scale),
                center_x = this._svgWidth / 2 - (viewBox.x + viewBox.width / 2 - this.transformParam.x) / pre_scale * next_scale,
                center_y = this._svgHeight / 2 - (viewBox.y + viewBox.height / 2 - this.transformParam.y) / pre_scale * next_scale;
            const t = d3.zoomIdentity.translate(center_x, center_y).scale(next_scale);
            this._svg.transition().duration(750).call(this._zoom.transform, t).on('end', () => {
                this.transformParam.x = t.x
                this.transformParam.y = t.y;
                this.transformParam.k = t.k;
            });
        }
    }
    // 自动节点聚焦
    onAutoNodeFouce(source) {
        const scale = this.transformParam.k,
            x = -source.y,
            y = -source.x;

        const center_x = x * scale + this._svgWidth / 2,
            center_y = y * scale + this._svgHeight / 2;
        const t = d3.zoomIdentity.translate(center_x, center_y).scale(scale);
        this._svg.transition()
            .duration(750)
            .call(this._zoom.transform, t).on('end', () => {
                this.transformParam.x = t.x
                this.transformParam.y = t.y;
                this.transformParam.k = t.k;
            });
    }
    // 显示右键菜单
    onContextmenu = (event, d) => {
        event.preventDefault()
        const transform = this.transformParam

        // 定位节点菜单位置
        this.contextMenuRef.setState({
            contextMenuVisible: true,
            menuTransform: {
                top: d.x * transform.k + transform.y + "px",
                left: d.y * transform.k + transform.x + "px"
            }
        })
    }
    // 拖拽式编辑
    draggable = () => {
        let _this = this,
            draggingTask = null,
            isCircleExpanded = false;
        function dragstarted(event, d) {
            const { layoutSet } = _this.state
            _this.dragObj.current = d;
            d.dx = 0;
            d.dy = 0;
            // 拖拽任务开始时，高亮节点、收缩其子节点、开启节点缓冲区
            draggingTask = setTimeout(() => {
                _this.handleSelectNode(d, this)
                d3.select(this).classed('dragging', true);
                if (d.children) {
                    d._children = d.children;
                    d.children = null;
                    _this.update(d, "drag");
                }
                _this._svgTree.selectAll('.ghost-circle')
                    .filter(n => !_this.isParent(n))
                    .filter(n => n !== d)
                    .filter(n => n !== d.parent)
                    .attr('r', 0)
                    .transition()
                    .duration(150)
                    .attr('r', (layoutSet.nodeSpace / 2 + layoutSet.nodeSize) * 0.9)
                    .on('end', () => {
                        isCircleExpanded = true;
                    });
            }, 150);
        }

        function dragged(event, d) {
            //移动的当前节点坐标：event的x，y是初始数据是被绑定拖拽事件节点的原位置，x轴方向向右，y轴向下
            d.dx = event.x - d.x + d.y
            d.dy = event.y - d.y + d.x
            d3.select(this)
                .attr('transform',
                    `translate(${d.dx}, ${d.dy})`);

            // 赋予目标节点和当前节点的坐标，用于创建缓冲区中的连线
            if (_this.dragObj.current && _this.dragObj.target) {
                _this.dragObj.line[0] = [
                    _this.dragObj.target.y,
                    _this.dragObj.target.x
                ];
                _this.dragObj.line[1] = [
                    d.dx,
                    d.dy,
                ];
                _this.createLink(
                    {
                        x: _this.dragObj.line[0][0],
                        y: _this.dragObj.line[0][1]
                    },
                    {
                        x: _this.dragObj.line[1][0],
                        y: _this.dragObj.line[1][1],
                    }
                );
            }
        }

        function dragended(event, d) {
            const { layoutSet, historyEdit, treeDepth } = _this.state,
                e_this = this
            d3.select(e_this).classed('dragging', false);
            _this._svgTree.selectAll('.dragging-target').classed('dragging-target', false);

            clearTimeout(draggingTask);

            if (isCircleExpanded) {
                _this._svgTree.selectAll('.ghost-circle')
                    .filter(n => n !== d)
                    .filter(n => n !== d.parent)
                    .attr('r', (layoutSet.nodeSpace / 2 + layoutSet.nodeSize) * 0.9)
                    .transition()
                    .duration(750)
                    .attr('r', 0);
            }
            isCircleExpanded = false;
            if (_this.dragObj.current && _this.dragObj.target && !_this.isParent(d)) {
                const prevParentNode = _this.dragObj.current.parent;
                // 拖拽操作完成，调整节点结构
                // 删除旧节点
                let index = _this.dragObj.current.parent.children.indexOf(d);
                if (index > -1) {
                    _this.dragObj.current.parent.children.splice(index, 1);
                    _this.dragObj.current.parent.data.children.splice(index, 1);
                    if (_this.dragObj.current.parent.children.length === 0) {
                        _this.dragObj.current.parent.children = null;
                    }
                }

                // 新增至新节点下        
                _this.dragObj.current.parent = _this.dragObj.target;
                _this.dragObj.current.data.parent_id = _this.dragObj.target.data.id;
                _this.dragObj.current.descendants().forEach(setDepth);
                if (_this.dragObj.target.children) {
                    _this.dragObj.target.children.push(_this.dragObj.current);
                } else if (_this.dragObj.target._children) {
                    _this.dragObj.target.children = _this.dragObj.target._children;
                    _this.dragObj.target._children = null;
                    _this.dragObj.target.children.push(_this.dragObj.current);
                } else {
                    _this.dragObj.target.children = [_this.dragObj.current];
                }
                _this.dragObj.target.data.children.push(_this.dragObj.current.data);

                d3.select(e_this)
                    .transition()
                    .duration(750)
                    .attr('transform', `translate(0, 0)`);
                d.dx = 0;
                d.dy = 0;

                // Step.3 刷新
                // let t = d3.zoomIdentity.translate(0, 0).scale(1)
                _this.update(_this._data, "drag");
                _this.setState({ treeDepth: getTreeMaxDepth(_this._data), historyEdit: historyEdit.concat([{ type: "drag", typeName: "拖拽", data: { prevParentNode: prevParentNode, currentNode: _this.dragObj.current, prevTreeDepth: treeDepth } }]) })
                _this.removeLink();
                // 将拖拽的节点坐标变为0
                _this.dragObj.current = null;
                _this.dragObj.target = null;
                message.success('拖拽更新成功!');
            } else {
                recoveryNode()
            }
            function recoveryNode() {
                _this.dragObj.target = null;
                d3.select(e_this)
                    .transition()
                    .duration(750)
                    .attr('transform', "translate(" + d.y + "," + d.x + ")");
                d.dx = 0;
                d.dy = 0;

                _this.removeLink();
                // 将拖拽的节点坐标变为0

                _this.dragObj.current = null;
            }
        }

        return d3
            .drag()
            .on('start', dragstarted)
            .on('drag', dragged)
            .on('end', dragended);
    }
    _click = (d) => {
        if (d.children || d._children) {
            // 有子节点收缩
            if (d.children) {
                d._children = d.children;
                d.children = null;
            } else {
                // 展开子节点展开
                d.children = d._children;
                d._children = null;
            }
            this.update(d, 'click');
        }
        if (this.state.nodeFocus) {
            this.onAutoNodeFouce(d)
        }
    }
    // 取消选择节点
    handleSvgClick = (e) => {
        const { selectNode } = this.state
        if (selectNode) {
            if (['circle', 'text'].indexOf(e.target.nodeName) === -1) {
                this.cancelSelectNode()
            }
        }
    }
    // 高亮节点
    handleSelectNode(node, dom) {
        const { layoutSet } = this.state
        const node_dom = d3.select(dom)

        this.cancelSelectNode()

        node_dom.selectChild(".node-circle")
            .attr("r", layoutSet.nodeSize * 1.5)
            .style("stroke", "#EE1C25")
            .style("stroke-width", 4)

        node.selected = true
        this.setState({ selectNode: { _node: node, _dom: dom } })
    }

    // 取消节点高亮
    cancelSelectNode = () => {
        const { layoutSet, selectNode, colorSet } = this.state

        if (selectNode) {
            if (selectNode._node.class === "found") {
                let parent = selectNode._node;
                while (parent !== null) {
                    parent.class = ""
                    parent = parent.parent;
                }
                this.setState({ selectNode: null })
                this.updateTreeDisplay()
            } else {
                selectNode._node.selected = false
                const node_dom = d3.select(selectNode._dom)
                node_dom.selectChild(".node-circle")
                    .attr("r", layoutSet.nodeSize)
                    .style("stroke", colorSet.nodeColor)
                    .style("stroke-width", d => d._children ? 2 : 0)
                this.setState({ selectNode: null })
            }
        }

    }
    // 收缩所有节点
    collapseAllElements = (e) => {
        if (e.children) {
            if (e.depth > 0) {
                e._children = e.children;
                delete e.children;
                e._children.forEach(e => this.collapseAllElements(e));
            } else {
                e.children.forEach(e => this.collapseAllElements(e));
            }
        }
    }
    // 扩展所有节点
    expandAllElements = (e) => {
        if (e._children) {
            e.children = e._children;
            delete e._children;
            e.children.forEach(e => this.expandAllElements(e));
        } else if (e.children) {
            e.children.forEach(e => this.expandAllElements(e));
        }
    }
    // 获取节点DOM
    _getNodeRef = (source) => {
        let treeNodes = this._svgTree.selectAll(".node")
        let parentNode = null
        treeNodes._groups[0].forEach(d => {
            if (d.__data__.id === source.id) {
                parentNode = d
            }
        })
        return parentNode;
    }
    // 节点拖拽至缓冲性时设置链接
    createLink(src, tar) {
        let shortLine = this._svgTree.selectAll('path.short-line');
        if (shortLine.size() === 0) {
            this._svgTree.append('path')
                .classed('short-line', true);
        }
        shortLine
            .attr('d', d => `M ${src.x}, ${src.y} L ${tar.x} ${tar.y}`);
    }
    // 节点拖拽出缓冲性时设置移出链接
    removeLink() {
        this._svgTree.selectAll('path.short-line')
            .attr('style', 'opacity:1')
            .attr('d', `M ${this.dragObj.line[0][0]}, ${this.dragObj.line[0][1]} L ${this.dragObj.line[1][0]}, ${this.dragObj.line[1][1]}`)
            .transition()
            .duration(50)
            .attr('style', 'opacity:1')
            .attr('d', `M ${this.dragObj.line[0][0]}, ${this.dragObj.line[0][1]} L ${this.dragObj.line[0][0]}, ${this.dragObj.line[0][1]}`)
            .remove();
    }
    isParent(nodeA) {
        let result = false;
        do {
            if (nodeA.parent === this.dragObj.current) {
                result = true;
                break;
            }
            nodeA = nodeA.parent
        } while (nodeA)
        return result;
    }
    // 删除节点
    handleRemoveNode = (deletedNode) => {
        // 默认展开并显示
        let { treeNodeCount, treeDepth, historyEdit } = this.state,
            curChildrenRef = deletedNode.children || deletedNode._children,
            parentChildrenRef = deletedNode.parent.children

        //获取当前删除节点的索引
        let index = parentChildrenRef.indexOf(deletedNode);

        //删除节点
        if (index > -1) {
            const parentNode = deletedNode.parent,
                preTreeNodeCount = treeNodeCount,
                preTreeDepth = treeDepth
            // 清除数据
            parentChildrenRef.splice(index, 1);
            deletedNode.parent.data.children.splice(index, 1);
            // 保留其子节点
            if (curChildrenRef) {
                curChildrenRef.forEach(child => {
                    child.parent = deletedNode.parent
                    child.depth = deletedNode.depth
                    child.data.parent_id = deletedNode.parent.data.id
                    child.descendants().forEach(setDepth);
                })

                parentChildrenRef.push(...curChildrenRef);
                deletedNode.parent.data.children.push(...deletedNode.data.children)
            }
            // 无子节点时，置null
            if (parentChildrenRef.length === 0) {
                deletedNode.parent.children = null
                deletedNode.parent._children = null
            }
            this.cancelSelectNode()
            this.setState(
                {
                    treeNodeCount: --treeNodeCount,
                    treeDepth: getTreeMaxDepth(this._data),
                    historyEdit: historyEdit.concat([{ type: "delete", typeName: "删除节点", data: { parentNode: parentNode, deletedNode: deletedNode, preTreeNodeCount, preTreeDepth } }])
                }
            )
            this.update(deletedNode, 'remove');
            message.success('删除节点成功!');
        }
    }
    handleRemoveNodeAndChildren = (deletedNode) => {
        let { treeNodeCount, treeDepth, historyEdit } = this.state,
            parentChildrenRef = deletedNode.parent.children,
            deleteNodesNum = 0;

        let index = parentChildrenRef.indexOf(deletedNode);

        if (index > -1) {
            getNodeChildren(deletedNode.data, []).forEach(d => {
                deleteNodesNum++
            })
            // 保存父节点
            const parentNode = deletedNode.parent,
                preTreeNodeCount = treeNodeCount,
                preTreeDepth = treeDepth

            parentChildrenRef.splice(index, 1);
            deletedNode.parent.data.children.splice(index, 1);
            // 无子节点时，清空
            if (parentChildrenRef.length === 0) {
                deletedNode.parent.children = null
            }
            this.cancelSelectNode()
            this.setState({ treeNodeCount: treeNodeCount - deleteNodesNum, treeDepth: getTreeMaxDepth(this._data), historyEdit: historyEdit.concat([{ type: "deleteSubTree", typeName: "删除节点及其子节点", data: { parentNode: parentNode, deletedNode: deletedNode, preTreeNodeCount, preTreeDepth } }]) })
            this.update(deletedNode, 'remove');
            message.success('成功删除节点及其子节点!');
        }
    }
    // 新增节点
    handleAddNode = async (targetNode, addNodeInfo) => {
        let { treeNodeCount, treeDepth, historyEdit } = this.state;
        addNodeInfo.parent_id = targetNode.data.id
        addNodeInfo.country_id = targetNode.data.country_id
        addNodeInfo.state_id = 0
        const parentNode = targetNode,
            preTreeNodeCount = treeNodeCount,
            preTreeDepth = treeDepth

        addNodeInfo.children = []
        addNodeInfo.id = treeNodeCount + 1
        // 使用d3.hierarchy从对象创建节点
        let newNode = d3.hierarchy(addNodeInfo)
        //添加深度、高度和父节点
        newNode.depth = targetNode.depth + 1
        newNode.parent = targetNode

        if (!targetNode.children) {
            targetNode.children = targetNode._children || []
            targetNode._children = null;
        }
        // 设置子节点
        targetNode.children.push(newNode)
        targetNode.data.children.push(newNode.data)
        // 更新总数，层次
        this.cancelSelectNode()
        this.setState({ treeNodeCount: ++treeNodeCount, treeDepth: Math.max(treeDepth, newNode.depth), historyEdit: historyEdit.concat([{ type: "add", typeName: "新增", data: { parentNode: parentNode, currentNode: newNode, preTreeNodeCount, preTreeDepth } }]) })
        this.update(targetNode, 'add')

        return true;
    }
    // 更新节点信息
    handleUpdateNode = async (newNodeInfo, updateNode) => {
        newNodeInfo.id = updateNode.data.id
        this.setState({ historyEdit: this.state.historyEdit.concat([{ type: "update", typeName: "修改", data: { prevNodeInfo: updateNode.data, currentNode: updateNode } }]) })
        updateNode.data = Object.assign({}, updateNode.data, newNodeInfo)
        this.cancelSelectNode()
        this.updateTreeDisplay();
        return true;
    }
    // 复制-> 粘贴节点
    handlePasteNode = (targetNode, nodeInfo) => {
        let copyNode = { ...nodeInfo.data },
            { treeNodeCount, treeDepth, historyEdit } = this.state;

        copyNode.parent_id = targetNode.data.id
        copyNode.country_id = targetNode.data.country_id
        copyNode.state_id = 0
        copyNode.children = [] // 清空子节点

        const parentNode = targetNode,
            preTreeNodeCount = treeNodeCount,
            preTreeDepth = treeDepth

        copyNode.id = treeNodeCount + 1;
        // 使用d3.hierarchy创建节点
        let newNode = d3.hierarchy(copyNode);
        //添加深度、高度和父节点
        newNode.depth = targetNode.depth + 1;
        newNode.parent = targetNode;
        this.collapseAllElements(newNode)
        if (!targetNode.children) {
            targetNode.children = targetNode._children || []
            targetNode._children = null
        }
        // 设置子节点
        targetNode.children.push(newNode);
        targetNode.data.children.push(newNode.data);
        // 更新总数，层次
        this.setState({
            treeNodeCount: ++treeNodeCount,
            treeDepth: Math.max(treeDepth, newNode.depth),
            historyEdit: historyEdit.concat([{ type: "copy", typeName: "复制", data: { parentNode: parentNode, currentNode: newNode, preTreeNodeCount, preTreeDepth } }])
        })
        this.update(targetNode, 'add');
        message.success('粘贴节点成功!');
    }
    // 复制节点及其子节点 -> 粘贴子树
    handlePasteNodeAndChildren = (targetNode, copyNode) => {
        let { treeNodeCount, treeDepth, historyEdit } = this.state,
            copyNodesNum = 0,
            maxDepth = 0;

        const parentNode = targetNode,
            preTreeNodeCount = treeNodeCount,
            preTreeDepth = treeDepth

        // 创建子树
        let newSubTree = Object.assign({}, copyNode.data, { parent_id: targetNode.data.id })
        let newNodes = d3.hierarchy(newSubTree);
        newNodes.parent = targetNode;

        // 计算复制节点总数，和深度
        newNodes.each(d => {
            d.depth = !d.parent ? 0 : d.parent.depth + 1;
            maxDepth = Math.max(d.depth, treeDepth)
            copyNodesNum++;
        });
        // 新父节点的子节点组中加入剪切节点
        if (targetNode.children) {
            targetNode.children.push(newNodes);
        } else if (targetNode._children) {
            targetNode.children = targetNode._children;
            targetNode._children = null;
            targetNode.children.push(newNodes);
        } else {
            targetNode.children = [newNodes];
        }
        targetNode.data.children.push(newNodes.data);
        // 更新总数，层次
        this.setState({ treeNodeCount: treeNodeCount + copyNodesNum, treeDepth: maxDepth, historyEdit: historyEdit.concat([{ type: "copySubtree", typeName: "复制节点及其子节点", data: { parentNode: parentNode, currentNode: newNodes, preTreeNodeCount, preTreeDepth } }]) })
        this.update(targetNode, 'add');
    }
    // 剪切节点
    handleCutNode = (d, dom) => {
        const { cutNode } = this.state,
            { cutNode_node, cutNode_dom } = cutNode

        //this.cancelSelectNode()
        if (cutNode_node) {
            d3.select(cutNode_dom).classed('cutting', false);
        }
        d3.select(dom).classed('cutting', true);
        this.setState({ cutNode: { cutNode_node: d, cutNode_dom: dom }, copyNode: null })
        if (d.children) {
            d._children = d.children;
            d.children = null;
            this.update(d);
        }
    }
    // 剪切 -> 粘贴
    handleCutPasteNode = (targetNode, { cutNode_node, cutNode_dom }) => {
        let { treeDepth, historyEdit } = this.state;
        if (targetNode.data.id === cutNode_node.parent.data.id) {
            message.error('不能在父节点上粘贴!');
            return;
        }
        // 记录原父节点
        const prevParentNode = cutNode_node.parent;
        // 关闭淡化
        d3.select(cutNode_dom).classed('cutting', false);
        // 删除剪切节点
        //获取当前删除节点的索引
        let index = cutNode_node.parent.children.indexOf(cutNode_node);
        //删除节点
        if (index > -1) {
            cutNode_node.parent.children.splice(index, 1);
            cutNode_node.parent.data.children.splice(index, 1);
            if (cutNode_node.parent.children.length === 0) {
                cutNode_node.parent.children = null;
            }
        }
        // 更新剪接节点的父节点
        cutNode_node.parent = targetNode;
        cutNode_node.data.parent_id = targetNode.data.id
        setDepth(cutNode_node)

        // 新增至新节点下
        if (targetNode.children) {
            targetNode.children.push(cutNode_node);
        } else if (targetNode._children) {
            targetNode.children = targetNode._children;
            targetNode._children = null;
            targetNode.children.push(cutNode_node);
        } else {
            targetNode.children = [cutNode_node];
        }
        targetNode.data.children.push(cutNode_node.data)
        this.cancelSelectNode()
        this.setState({ cutNode: { cutNode_node: null, cutNode_dom: null }, treeDepth: getTreeMaxDepth(this._data), historyEdit: historyEdit.concat([{ type: "cut", typeName: "剪切", data: { prevParentNode: prevParentNode, currentNode: cutNode_node, prevTreeDepth: treeDepth } }]) })
        this.update(this._data, "cut");
        message.success('粘贴节点成功!');
    }
    // 撤回拖拽和剪切操作
    handleUndoDragOrCut = (record) => {
        let { prevParentNode, currentNode, prevTreeDepth } = record,
            parentChildrenRef = currentNode.parent.children || currentNode.parent._children;

        //获取当前删除节点的索引
        let index = parentChildrenRef.indexOf(currentNode);
        //删除节点
        if (index > -1) {
            parentChildrenRef.splice(index, 1);
            currentNode.parent.data.children.splice(index, 1);
            // 无子节点时，置null
            if (parentChildrenRef.length === 0) {
                currentNode.parent.children = null
                currentNode.parent._children = null
            }
        }
        // 拖拽操作撤回
        currentNode.parent = prevParentNode;
        currentNode.data.parent_id = prevParentNode.data.id
        currentNode.descendants().forEach(setDepth);
        // 新增至原父节点节点下
        if (prevParentNode.children) {
            prevParentNode.children.push(currentNode);
        } else if (prevParentNode._children) {
            prevParentNode.children = prevParentNode._children;
            prevParentNode._children = null;
            prevParentNode.children.push(currentNode);
        } else {
            prevParentNode.children = [currentNode];
        }
        prevParentNode.data.children.push(currentNode.data)
        this.setState({ treeDepth: prevTreeDepth, historyEdit: this.state.historyEdit.slice(0, -1) })
        this.update(this._data, 'drag');
        message.success('撤回成功!');
    }
    // 撤回删除节点
    handleUndoDeleteNode = (record) => {
        let { historyEdit } = this.state,
            updateChildren = [],
            { parentNode, deletedNode, preTreeNodeCount, preTreeDepth } = record,
            objectChildren = deletedNode.children || deletedNode._children,
            parentChildren = parentNode.children || parentNode._children ? parentNode.children || parentNode._children : parentNode.children = [];

        // 更新其子节点的父ID
        if (objectChildren) {
            objectChildren.forEach(child => {
                updateChildren.push({ id: child.data.id, parent_id: deletedNode.data.id })
            })
        }
        // 恢复节点
        parentChildren.push(deletedNode)
        parentNode.data.children.push(deletedNode.data)
        // 恢复其所属子节点
        if (objectChildren) {
            objectChildren.forEach(child => {
                //获取当前删除节点的索引
                let index = parentChildren.indexOf(child);
                //删除节点
                if (index > -1) {
                    parentChildren.splice(index, 1);
                    parentNode.data.children.splice(index, 1);
                }
                child.parent = deletedNode
                child.data.parent_id = deletedNode.data.id
            })
        }
        // 设置深度
        deletedNode.descendants().forEach(setDepth);
        // 展开子节点
        if (!deletedNode.children) {
            deletedNode.children = deletedNode._children
            deletedNode._children = null;
        }
        if (!parentNode.children) {
            parentNode.children = parentNode._children
            parentNode._children = null;
        }
        this.setState({ treeNodeCount: preTreeNodeCount, treeDepth: preTreeDepth, historyEdit: historyEdit.slice(0, -1) })
        this.update(deletedNode, 'add');
        message.success('撤回成功!');

    }
    // 撤回删除节点及其子节点
    handleUndoDeleteSubTree = (record) => {
        let { historyEdit } = this.state,
            { parentNode, deletedNode, preTreeNodeCount, preTreeDepth } = record,
            deleteNodes = [];

        // 遍历节点及其子节点
        getNodeChildren(deletedNode.data, []).forEach(d => {
            deleteNodes.push({ id: d.id, state_id: 0 })
        })
        // 展开父节点
        if (!parentNode.children) {
            parentNode.children = parentNode._children || []
            parentNode._children = null
        }
        // 恢复节点及其子节点
        parentNode.children.push(deletedNode)
        parentNode.data.children.push(deletedNode.data)

        deletedNode.descendants().forEach(setDepth);

        this.setState({ treeNodeCount: preTreeNodeCount, treeDepth: preTreeDepth, historyEdit: historyEdit.slice(0, -1) })
        this.update(deletedNode, 'add')
        message.success('撤回成功!')
    }
    // 撤回新增和复制节点
    handleUndoAddOrCopyNode = (record) => {
        let { parentNode, currentNode, preTreeNodeCount, preTreeDepth } = record,
            parentChildrenRef = parentNode.children || parentNode._children;

        //获取当前删除节点的索引
        let index = parentChildrenRef.indexOf(currentNode);
        // 清除数据
        parentChildrenRef.splice(index, 1);
        parentNode.data.children.splice(index, 1);
        // 无子节点时，置null
        if (parentChildrenRef.length === 0) {
            parentNode.children = null
            parentNode._children = null
        }
        this.setState({ treeNodeCount: preTreeNodeCount, treeDepth: preTreeDepth, historyEdit: this.state.historyEdit.slice(0, -1) })
        this.update(currentNode, 'remove');
        message.success('撤回成功!');
    }
    // 撤回复制节点及其子节点
    handleUndoCopySubTree = (record) => {
        let { parentNode, currentNode, preTreeNodeCount, preTreeDepth } = record,
            deleteNodes = [],
            parentChildrenRef = parentNode.children || parentNode._children

        // 遍历节点及其子节点
        getNodeChildren(currentNode.data, []).forEach(d => {
            deleteNodes.push(d.id)
        })
        let index = parentChildrenRef.indexOf(currentNode);
        parentChildrenRef.splice(index, 1);
        parentNode.data.children.splice(index, 1);
        // 无子节点时，清空
        if (parentChildrenRef.length === 0) {
            parentNode.children = null
            parentNode._children = null
        }
        this.setState({ treeNodeCount: preTreeNodeCount, treeDepth: preTreeDepth, historyEdit: this.state.historyEdit.slice(0, -1) })
        this.update(currentNode, 'remove');
        message.success('撤回成功!');
    }
    // 撤回更新节点
    handleUndoUpdateNode = (record) => {
        let { prevNodeInfo, currentNode } = record
        currentNode.data = Object.assign({}, currentNode.data, prevNodeInfo)
        this.setState({ historyEdit: this.state.historyEdit.slice(0, -1) })
        this.updateTreeDisplay();
        message.success('撤回成功!');
    }
    handleUndo = () => {
        const { historyEdit } = this.state,
            lastOperation = historyEdit[historyEdit.length - 1]

        if (historyEdit.length === 0) {
            message.error('无历史操作记录!');
            return;
        }
        switch (lastOperation.type) {
            case "drag":
                this.handleUndoDragOrCut(lastOperation.data)
                break;
            case "delete":
                this.handleUndoDeleteNode(lastOperation.data)
                break;
            case "deleteSubTree":
                this.handleUndoDeleteSubTree(lastOperation.data)
                break;
            case "add":
                this.handleUndoAddOrCopyNode(lastOperation.data)
                break;
            case "update":
                this.handleUndoUpdateNode(lastOperation.data)
                break;
            case "copy":
                this.handleUndoAddOrCopyNode(lastOperation.data)
                break;
            case "copySubtree":
                this.handleUndoCopySubTree(lastOperation.data)
                break;
            case "cut":
                this.handleUndoDragOrCut(lastOperation.data)
                break;
            default:
                break;
        }

    }
    handleTreeSettingChange = (set, setType) => {
        switch (setType) {
            case 'layoutSet':
                const layoutSet = { ...this.state.layoutSet }
                this.setState({ layoutSet: Object.assign({}, layoutSet, { [set.layoutType]: set.value }) })
                break;
            case 'colorSet':
                const colorSet = { ...this.state.colorSet }
                this.setState({ colorSet: Object.assign({}, colorSet, { [set.colorType]: set.value }) })
                break;
            case 'copyNode':
                const { cutNode } = this.state,
                    { cutNode_node, cutNode_dom } = cutNode
                if (cutNode_node) {
                    d3.select(cutNode_dom).classed('cutting', false);
                }
                this.setState({ copyNode: set, cutNode: { cutNode_node: null, cutNode_dom: null } })
                break;
            case 'cutNode':
                this.setState({ cutNode: { cutNode_node: set.data, cutNode_dom: set.dom } })
                break;
            case 'nodeFocus':
                this.setState({ nodeFocus: set })
                break;
            case 'autoZoomTree':
                this.setState({ autoZoomTree: set })
                break;
            case 'selectNode':
                this.setState({ selectNode: set })
                break;
            case 'infoBarVisible':
                this.setState({ infoBarVisible: set })
                break;
            case 'isNodeLazyLoading':
                this.setState({ isNodeLazyLoading: set })
                break;
            default:
                break;
        }
    }

    render() {
        const { selectNode, layoutSet, colorSet, copyNode, cutNode, cur_treeData, nodeFocus, autoZoomTree, infoBarVisible, isNodeLazyLoading, treeNodeCount, treeDepth, historyEdit } = this.state
        return (
            <div ref={this.mainview} className="mainview" >
                <h4 style={{position:'absolute',width:'100%',textAlign: 'center',color:'red'}}>温馨提示: 1.鼠标右键点击节点显示编辑菜单栏. 2.扩展节点过多时，开启右上角设置面板中的节点懒加载. 3. 撤回快捷键:ctrl+z</h4>
                <TreeInfoPanel
                    onRef={ref => (this.infoPanelRef = ref)}
                    selectNode={selectNode}
                    cur_treeData={cur_treeData}
                    copyNode={copyNode}
                    cutNode={cutNode}
                    infoBarVisible={infoBarVisible}
                    treeNodeCount={treeNodeCount}
                    treeDepth={treeDepth}
                    onTreeSettingChange={this.handleTreeSettingChange} />
                <ContextMenu
                    onRef={ref => (this.contextMenuRef = ref)}
                    selectNode={selectNode}
                    layoutSet={layoutSet}
                    colorSet={colorSet}
                    copyNode={copyNode}
                    cutNode={cutNode}
                    historyEdit={historyEdit}
                    onAddNode={this.handleAddNode}
                    onUpdateNode={this.handleUpdateNode}
                    onRemoveNode={this.handleRemoveNode}
                    onPasteNode={this.handlePasteNode}
                    onPasteNodeAndChildren={this.handlePasteNodeAndChildren}
                    onRemoveNodeAndChildren={this.handleRemoveNodeAndChildren}
                    onCutNode={this.handleCutNode}
                    onCutPasteNode={this.handleCutPasteNode}
                    onCancelSelectNode={this.cancelSelectNode}
                    onTreeSettingChange={this.handleTreeSettingChange}
                    onUndo={this.handleUndo} />
                <TreeSettingsPanel layoutSet={layoutSet} colorSet={colorSet} nodeFocus={nodeFocus} autoZoomTree={autoZoomTree} infoBarVisible={infoBarVisible} isNodeLazyLoading={isNodeLazyLoading} onTreeSettingChange={this.handleTreeSettingChange} />
                <ExternalButton
                    getTreeRef={this.treeRef} />

            </div>
        );
    }
}


function getTreeMaxDepth(root) {
    let depth = 0;
    function countDepth(node) {
        depth = Math.max(node.depth, depth)
        if (node.children) {
            node.children.forEach(countDepth);
        } else if (node._children) {
            node._children.forEach(countDepth);
        }
    }
    countDepth(root)
    return depth
}

function setDepth(d) {
    d.depth = !d.parent ? 0 : d.parent.depth + 1;
    if (d.children) {
        d.children.forEach(setDepth);
    } else if (d._children) {
        d._children.forEach(setDepth);
    }
}

// 返回当前子树节点数组
function getNodeChildren(e, array) {
    array.push(e)
    e.children.forEach(e => getNodeChildren(e, array));
    return array;
}
export default Tree;