import React from 'react';
import { Button, Tooltip } from 'antd'
import { ShrinkOutlined, ArrowsAltOutlined } from '@ant-design/icons';
import './index.scss'


function ExternalButton(props) {
    const { getTreeRef } = props;

    const handleCollapseAllNodes = () => {
        getTreeRef.collapseAllElements(getTreeRef._data);
        getTreeRef.update(getTreeRef._data, "collapseAllElements");
        setTimeout(() => {
            getTreeRef.onAutoNodeFouce(getTreeRef._data);
        }, 500);
    };
    const handleExpandAllNodes = () => {
        getTreeRef.expandAllElements(getTreeRef._data);
        getTreeRef.update(getTreeRef._data, "expandAllElements");
        // setTimeout(() => {
        //     getTreeRef.onAutoZoomTree();
        // }, 500);
    };
    return (
        <div className="external-btn-panel">
            <Tooltip placement="left" title="扩展所有节点">
                <Button className="external-btn" shape="circle" onClick={handleExpandAllNodes} icon={<ArrowsAltOutlined />} />
            </Tooltip>
            <Tooltip placement="left" title="收缩所有节点">
                <Button className="external-btn" shape="circle" onClick={handleCollapseAllNodes} icon={<ShrinkOutlined />} />
            </Tooltip>
        </div>
    );
}

export default ExternalButton;