import React from 'react';
import './index.scss'
import { Menu, message, Modal, Button, Form, Input } from 'antd'
const { SubMenu } = Menu;

class ContextMenu extends React.Component {
    constructor(props) {
        super(props);
        this.menuType = null; // 菜单处理类型add / update
        this.cilckType = null; // 点击菜单触发事件类型 cut/ copy_cur /copy_curAndCild
        this.formRef = React.createRef()
        this.isStartEdit = true // 编辑使能
        this.state = {
            modalvisible: false, // 表单显隐
            submitLoading: false, // 表单提交请求状态
            contextMenuVisible: false, // 菜单显隐
            menuTransform: {
                top: null,
                left: null
            },
            editBtnPopVisible: false,
            isNoEditRole: true
        };
    }
    componentDidMount() {
        if (this.props.onRef)
            this.props.onRef(this);
    }
    UNSAFE_componentWillUpdate(newProps, newState, newContext) {
        // state更新前,执行
        // 显示右键菜单，则取消监听快捷键
        if (newState.contextMenuVisible) {
            document.removeEventListener("keydown", this.handleKeyDown);
        } else {
            document.addEventListener("keydown", this.handleKeyDown);
        }
    }
    componentDidUpdate(prevProps, prevState) {
        // state更新后,执行
        const { modalvisible } = this.state,
            { selectNode } = this.props
        if (!prevState.modalvisible && modalvisible && this.menuType === 'update') {
            this.formRef.current.setFieldsValue(selectNode._node.data)
        }

    }
    // 隐藏菜单
    closeMenu = (markClose) => {
        const { onCancelSelectNode } = this.props

        this.setState({ contextMenuVisible: false })
        if (markClose) {
            onCancelSelectNode()
        }
    }
    // 新增节点
    handleCreateNode() {
        const { selectNode } = this.props
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        }
        this.menuType = "create"
        this.setState({ modalvisible: true })
    }
    // 删除当前节点
    handleDelete_cur() {
        const { selectNode, onRemoveNode } = this.props
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能删除国家根节点');
            return;
        }
        const delete_cur_mes = message.warning(<> 是否删除该节点? <div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelDeleteNode}>取消 </Button> <Button type="primary" size="small" onClick={confirmDeleteNode}>确定 </Button></div> </>, 0);
        function cancelDeleteNode() {
            setTimeout(delete_cur_mes, 1)
        }
        function confirmDeleteNode() {
            onRemoveNode(selectNode._node)
            setTimeout(delete_cur_mes, 1)
        }
    }
    // 删除当前节点及其子节点
    handleDelete_curAndCild() {
        const { selectNode, onRemoveNodeAndChildren } = this.props
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能删除国家根节点');
            return;
        }
        const delete_curAndCild_mes = message.warning(<> 是否删除节点及其子节点? <div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelDeleteNodes}>取消 </Button> <Button type="primary" size="small" onClick={confirmDeleteNodes}>确定 </Button></div> </>, 0);
        function cancelDeleteNodes() {
            setTimeout(delete_curAndCild_mes, 1)
        }
        function confirmDeleteNodes() {
            onRemoveNodeAndChildren(selectNode._node)
            setTimeout(delete_curAndCild_mes, 1)
        }
    }
    // 获取表单节点信息
    handleUpdateNode() {
        const { selectNode } = this.props;
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能修改国家根节点');
            return;
        }
        this.menuType = "update"
        this.setState({ modalvisible: true })
    }
    // 剪切节点
    handleCutNode() {
        const { selectNode, onCutNode } = this.props,
            _this = this;
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能剪切国家根节点');
            return;
        }
        const cut_mes = message.warning(<> 是否剪切节点? <br /> 提示: 统计信息栏可取消剪切状态<div style={{ padding: "12px 0 4px" }}><Button size="small" onClick={cancelCut}>取消 </Button> <Button type="primary" size="small" onClick={confirmCut}>确定 </Button></div> </>, 0);
        function cancelCut() {
            setTimeout(cut_mes, 1)
        }
        function confirmCut() {
            setTimeout(cut_mes, 1)
            onCutNode(selectNode._node, selectNode._dom)
            message.success('节点已加入剪切板!');
            _this.cilckType = "cut"
        }
    }
    // 复制当前节点
    handleCopy_cur() {
        const { selectNode, onTreeSettingChange } = this.props
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能删除国家根节点');
            return;
        }
        this.cilckType = "copy_cur"
        onTreeSettingChange(selectNode._node, "copyNode")
        message.success('已复制节点!');
    }
    // 复制节点及其子节点
    handleCopy_curAndCild() {
        const { selectNode, onTreeSettingChange } = this.props
        if (!selectNode) {
            message.error('请先选中节点');
            return;
        } else if (selectNode._node.parent === null) {
            message.error('不能删除国家根节点');
            return;
        }
        onTreeSettingChange(selectNode._node, "copyNode")
        message.success('已复制节点!');
        this.cilckType = "copy_curAndCild"
    }
    // 粘贴节点
    handlePasteNode() {
        const { selectNode, copyNode, cutNode, onPasteNode, onCutPasteNode, onPasteNodeAndChildren } = this.props

        if (!copyNode && !cutNode.cutNode_node) {
            message.error('请先复制/剪切节点');
            return;
        }
        if (this.cilckType === "copy_cur") {
            onPasteNode(selectNode._node, copyNode)
        } else if (this.cilckType === "copy_curAndCild") {
            onPasteNodeAndChildren(selectNode._node, copyNode)
        } else if (this.cilckType === "cut") {
            onCutPasteNode(selectNode._node, cutNode)
        }
    }
    // 单击事件
    handleClick = (e) => {
        this.closeMenu()
        if (this.isStartEdit) {
            switch (e.key) {
                // 新建节点
                case 'create':
                    this.handleCreateNode()
                    break;
                // 删除节点
                case 'delete_cur':
                    this.handleDelete_cur()
                    break;
                // 删除节点及其子节点
                case 'delete_curAndCild':
                    this.handleDelete_curAndCild()
                    break;
                // 修改节点
                case 'update':
                    this.handleUpdateNode()
                    break;
                // 剪切节点
                case 'cut':
                    this.handleCutNode()
                    break;
                // 复制当前节点
                case 'copy_cur':
                    this.handleCopy_cur()
                    break;
                // 复制当前节点及其子节点
                case 'copy_curAndCild':
                    this.handleCopy_curAndCild()
                    break;
                // 粘贴节点
                case 'paste':
                    this.handlePasteNode()
                    break;
                default:
                    break;
            }
        } else {
            message.error("暂不支持编辑！")
        }
    }
    // 快捷键事件
    handleKeyDown = (e) => {
        if (e.ctrlKey && [65, 75, 77, 88, 67, 86, 90].indexOf(e.keyCode) !== -1) {
            e.preventDefault()

            if (this.isStartEdit) {
                switch (e.keyCode) {
                    case 65:
                        // ctrl+a 新增
                        this.handleCreateNode()
                        break;
                    case 75:
                        // ctrl+k 删除节点
                        this.handleDelete_cur()
                        break;
                    case 77:
                        // ctrl +m 修改节点
                        this.handleUpdateNode()
                        break;
                    case 88:
                        // ctrl+x 剪切
                        this.handleCutNode()
                        break;
                    case 67:
                        // ctrl+c 复制当前节点
                        this.handleCopy_cur()
                        break;
                    case 86:
                        // ctrl+v 粘贴
                        this.handlePasteNode()
                        break;
                    case 90:
                        // ctrl+z 撤回历史编辑操作
                        this.props.onUndo()
                        break;
                    default:
                        break;
                }
            } else {
                message.error("暂不支持编辑！")
            }
        }
    }

    // 取消表单弹窗
    handleCancel = () => {
        this.setState({ submitLoading: false, modalvisible: false })
        this.onReset()
    }
    // 表单新增节点
    onAddFinish = (newNodeInfo) => {
        let _this = this
        const { selectNode } = _this.props
        _this.setState({ submitLoading: true })
        const node = {
            name: newNodeInfo.name,
            description: newNodeInfo.description
        }
        this.props.onAddNode(selectNode._node, node).then(info => {
            if (info) {
                _this.setState({ submitLoading: false, modalvisible: false })
                _this.onReset()
                message.success('新增节点成功!');
            }

        })
    };
    // 表单更新
    onUpdateFinish = (newNodeInfo) => {
        let _this = this
        const { selectNode } = _this.props
        _this.setState({ submitLoading: true })
        this.props.onUpdateNode(newNodeInfo, selectNode._node).then(info => {
            if (info) {
                _this.setState({ submitLoading: false, modalvisible: false })
                _this.onReset()
                message.success('更新节点成功!');
            } else {
                message.error('更新节点失败!');
            }

        })
    };
    // 重置表单内容
    onReset = () => {
        this.formRef.current.resetFields();
    };
    render() {
        //editBtnPopVisible
        const { modalvisible, submitLoading, contextMenuVisible, menuTransform } = this.state
        const { copyNode, cutNode } = this.props
        return (
            <>
                <div className="menu" style={contextMenuVisible ? { display: 'block', top: menuTransform.top, left: menuTransform.left } : { display: 'none' }}>
                    <div className="menu-mask" onClick={() => { this.closeMenu(1); }}></div>
                    <Menu className="menu-box" onClick={this.handleClick} selectable={false}>
                        <Menu.Item className="menu-item" key="create"  >新建节点 (ctrl+a)</Menu.Item>
                        <SubMenu key="delete" popupClassName="menu-item" title="删除节点">
                            <Menu.Item className="subMenu-item" key="delete_cur">删除该节点 (ctrl+k)</Menu.Item>
                            <Menu.Item className="subMenu-item" key="delete_curAndCild">删除该节点及其子节点</Menu.Item>
                        </SubMenu>
                        <Menu.Item className="menu-item menu-divider" key="update"  >修改节点 (ctrl+m)</Menu.Item>
                        <Menu.Item className="menu-item" key="cut"  >剪切节点 (ctrl+x)</Menu.Item>
                        <SubMenu key="copy" popupClassName="menu-item" title="复制节点" >
                            <Menu.Item className="subMenu-item" key="copy_cur">复制该节点 (ctrl+c)</Menu.Item>
                            <Menu.Item className="subMenu-item" key="copy_curAndCild">复制该节点及其子节点</Menu.Item>
                        </SubMenu>
                        <Menu.Item className="menu-item" key="paste" disabled={copyNode || cutNode.cutNode_node ? false : true}  >粘贴节点 (ctrl+v)</Menu.Item>
                    </Menu>
                </div>
                <Modal
                    title={this.menuType === "create" ? "新增目标" : "更新目标"}
                    visible={modalvisible}
                    onCancel={this.handleCancel}
                    footer={null}
                >
                    <Form
                        name="basic"
                        labelCol={{
                            xs: { span: 24 },
                            sm: { span: 6 },
                        }}
                        wrapperCol={{
                            xs: { span: 24 },
                            sm: { span: 16 },
                        }}
                        ref={this.formRef}
                        onFinish={this.menuType === "create" ? this.onAddFinish : this.onUpdateFinish}
                    >
                        <Form.Item
                            label="目标名字"
                            name="name"
                            rules={[{ required: true, message: '请输入目标名字!' }]}
                        >
                            <Input autoComplete="off" />
                        </Form.Item>
                        <Form.Item name="description" label="目标信息">
                            <Input.TextArea rows={4} allowClear />
                        </Form.Item>
                        <Form.Item wrapperCol={{ offset: 6, span: 16 }}>
                            <Button type="primary" htmlType="submit" style={{ marginRight: '8px' }} loading={submitLoading}>
                                提交
                            </Button>
                            <Button htmlType="button" onClick={this.onReset}>
                                重置
                            </Button>
                        </Form.Item>
                    </Form>
                </Modal>
            </>
        )
    }
}

export default ContextMenu;


