import React from 'react';
import { Button, Card, Row, Col, Slider, InputNumber, Popover, Switch } from 'antd';
import { UnorderedListOutlined, CloseOutlined } from '@ant-design/icons';
import { ChromePicker } from 'react-color'
import './index.scss';
// 图谱控制面板
class TreeSettingPanel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showSettingCard: false, // 显示隐藏控制面板
            key: 'tab1', // 标签页索引
        };
    }
    handleCloseCard = (e) => {
        this.setState({ showSettingCard: false })
    }
    handleOpenCard = (e) => {
        this.setState({ showSettingCard: true });
    }
    onTabChange = (key, type) => {
        this.setState({ [type]: key });
    }
    handleChangeColor = (color, type) => {
        this.props.onTreeSettingChange({ value: color.hex, colorType: type }, "colorSet")
    }
    render() {
        const { showSettingCard, key } = this.state
        const { layoutSet, colorSet, nodeFocus, autoZoomTree, infoBarVisible, isNodeLazyLoading, onTreeSettingChange } = this.props
        const tabList = [{ key: 'tab1', tab: '布局设置' }, { key: 'tab2', tab: "样式设置" }]
        const layoutSetList = [
            { name: "节点间距", min: 0, max: 100, step: 1, value: layoutSet.nodeSpace, valueName: "nodeSpace" },
            { name: "节点大小", min: 1, max: 30, step: 1, value: layoutSet.nodeSize, valueName: "nodeSize" },
            { name: "边长度", min: 0, max: 1000, step: 10, value: layoutSet.linkLength, valueName: "linkLength" },
            { name: "边宽度", min: 0.5, max: 10, step: 0.5, value: layoutSet.linkWidth, valueName: "linkWidth" },
            { name: "标签大小", min: 1, max: 30, step: 1, value: layoutSet.labelSize, valueName: "labelSize" },
        ]
        const styleSetList = [
            { name: "节点轮廓颜色", value: colorSet.nodeColor, valueName: "nodeColor" },
            { name: "边颜色", value: colorSet.linkColor, valueName: "linkColor" },
        ]
        const switchBtnList = [
            { name: "节点跟踪聚焦", value: nodeFocus, valueName: "nodeFocus" },
            { name: "自适应缩放", value: autoZoomTree, valueName: "autoZoomTree" },
            { name: "统计信息栏", value: infoBarVisible, valueName: "infoBarVisible" },
            { name: "节点懒加载", value: isNodeLazyLoading, valueName: "isNodeLazyLoading" },
        ]
        const contentList = {
            tab1: <div>
                <Row  justify="space-between">
                    {
                        layoutSetList.map((set) => {
                            return (
                                <React.Fragment key={set.valueName}>
                                    <Col span={24} className="tree-settings-title">{set.name}</Col>
                                    <Col span={17}>
                                        <Slider min={set.min} max={set.max} step={set.step} value={set.value} onChange={value => { onTreeSettingChange({ value: value, layoutType: set.valueName }, "layoutSet") }} />
                                    </Col>
                                    <Col span={6}>
                                        <InputNumber min={set.min} max={set.max} step={set.step} value={set.value} onChange={value => { onTreeSettingChange({ value: value, layoutType: set.valueName }, "layoutSet") }} />
                                    </Col>
                                </React.Fragment>
                            )
                        })
                    }
                </Row>
                {
                    switchBtnList.map(set => {
                        return (
                            <Row style={{ marginTop: '16px' }} justify="space-between" key={set.valueName} >
                                <Col className="tree-settings-title" span={17}>
                                    {set.name}
                                </Col>
                                <Col span={6}>
                                    <Switch checked={set.value} onChange={value => onTreeSettingChange(value, set.valueName)} />
                                </Col>
                            </Row>
                        )
                    })
                }
            </div>,
            tab2: <div>
                <Row gutter={[0, 16]} justify="space-between" >
                    {
                        styleSetList.map((set) => {
                            return (
                                <React.Fragment key={set.valueName}>
                                    <Col className="tree-settings-title" span={17}>
                                        {set.name}
                                    </Col>
                                    <Col span={6}>
                                        <Popover content={<ChromePicker color={set.value} disableAlpha={true} onChangeComplete={color => this.handleChangeColor(color, set.valueName)} />} trigger="click" overlayClassName="site-antd-popover">
                                            <span className="tree-settings-colorBox" style={{ backgroundColor: set.value }}></span>
                                        </Popover>
                                    </Col>
                                </React.Fragment>
                            )
                        })
                    }
                </Row>
            </div>
        }
        return (
            <>
                <Button className="tree-settings-btn" shape="circle" size="large" onClick={this.handleOpenCard} icon={<UnorderedListOutlined />} />
                <Card
                    size="small"
                    className="tree-settings-card"
                    style={showSettingCard ? { display: 'block' } : { display: 'none' }}
                    bodyStyle={{ padding: "15px" }}
                    tabList={tabList}
                    activeTabKey={key}
                    tabBarExtraContent={<Button shape="circle" icon={<CloseOutlined />} onClick={this.handleCloseCard}></Button>}
                    onTabChange={key => {
                        this.onTabChange(key, 'key');
                    }}>
                    {contentList[this.state.key]}
                </Card>
            </>
        )
    }
}

export default TreeSettingPanel;